import React from 'react';
import classes from './dashboard.module.css';
import TodoItem from '../../components/todoItem';

class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            items: [],
        }
        this.addNew = this.addNew.bind(this);
    }

    addNew() {
        console.log(this._inputElement.value); //this._inputElement is an element that defines the input
        if (this._inputElement.value !== "") {
            const newItem = {
                title: this._inputElement.value,
                key: Date.now()
            }
            // this.setState({items: this._inputElement.value}, () => console.log(this.state.items));
            //get the current items and concat the new item

            //method1
            // this.setState((prevState) => {
            //     return {items: prevState.items.concat(newItem)};
            // }, 
            // () => console.log(this.state.items) //to see the latest update
            // );

            //method2
            this.setState({items: [...this.state.items, newItem]}, () => console.log(this.state.items));

            this._inputElement.value = '';
        }
        //push to an array

    }

    delete(value) {
        console.log("key", value);
        const filterItem = this.state.items.filter( list => list.key !== value);
        this.setState({items:filterItem});
    }

    render() {
        return(
            <div className={classes.dashboard_container}>
                <div className={classes.search}>
                    <input className={classes.input} type="text" placeholder="To do list" ref={(a) => this._inputElement = a} />
                    <button className={classes.btn} onClick={this.addNew}>Submit</button>
                </div>
                <div className={classes.todo}>
                    {
                        this.state.items.map((list) => {
                            return <TodoItem title={list.title} time={list.key} click={() => this.delete(list.key)}/>
                        })

                        // this.state.items.map( (list) => <TodoItem />)
                    }
                    {/* <TodoItem />
                    <TodoItem />
                    <TodoItem /> */}
                </div>
            </div>
            
        );
    }
}
export default Dashboard;